.SLRElemlentWrapper {padding: 30px !important;}

.SRLStage{margin-bottom: 20px !important}

.thumbnails {max-width: 300px !important}

.SRLCaptionContainer,.SLRCaptionContainer {padding:4px 0 !important}
.SRLCaptionText {line-height: 5px !important;}
.SRLThumbnailsContainer{grid-gap:4px !important; padding-bottom:4px !important}
.SRLThumb{background-size: contain !important;}
.SRLElementContainer {height: calc(100vh - 185px) !important;}
.fullOpacity{opacity: 1 !important}

.Img {cursor: pointer; }

.Img:hover {transform: scale(1.05);
    opacity: 1 !important;}
    
.Img:hover{animation: shake 0.5s;
        animation-iteration-count: 1;}
@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    /* // 10% { transform: translate(-1px, -2px) rotate(-1deg); } */
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    /* // 30% { transform: translate(3px, 2px) rotate(0deg); } */
    40% { transform: translate(1px, -1px) rotate(1deg); }
    /* // 50% { transform: translate(-1px, 2px) rotate(-1deg); } */
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    /* // 70% { transform: translate(3px, 1px) rotate(-1deg); } */
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    /* // 90% { transform: translate(1px, 2px) rotate(0deg); } */
    100% { transform: translate(1px, -2px) rotate(-1deg); }
}
